<template>
  <div>
    <v-app-bar app color="primary" dark>
      <v-btn icon @click.stop="$router.go(-1)">
        <v-icon>mdi-arrow-left-circle</v-icon>
      </v-btn>
      <v-toolbar-title to="/">{{ appName }}</v-toolbar-title>
    </v-app-bar>

    <template>
      <v-card>
        <v-toolbar dark color="primary">
          <v-toolbar-title>Data Siswa </v-toolbar-title>
        </v-toolbar>
        <v-divider></v-divider>
        <v-data-table :headers="headers" :items="info" class="elevation-1">
          <template v-slot:items="props">
            <td>{{ props.item.id }}</td>
            <td>{{ props.item.NAME }}</td>
            <td>{{ props.item.DOB }}</td>
            <td>{{ props.item.NIK }}</td>
            <td>{{ props.item.NISN }}</td>
            <td>{{ props.item.ADDRESS }}</td>
          </template>
        </v-data-table>
      </v-card>
    </template>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data: () => ({
    categories: [],
    books: [],
    headers: [
      { text: "NO", value: "id" },
      { text: "NAMA LENGKAP", value: "NAME" },
      { text: "TANGGAL LAHIR", value: "DOB" },
      { text: "NIK", value: "NIK" },
      { text: "NISN", value: "NISN" },
      { text: "ADDRESS", value: "ADDRESS" },
    ],
    info: [],
  }),
  mounted() {
    let config = {
      headers: {
        Authorization: "Bearer " + this.user.token,
      },
    };
    this.axios.post("/api/getRegistered", {}, config).then((response) => {
      this.info = response.data;
      //console.log(this.info);
    });
  },
  computed: {
    ...mapGetters({
      user: "auth/user",
    }),
  },
};
</script>